import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';


@Component({
    selector: 'app-sidebar',
    templateUrl: './sidebar.component.html',
    styleUrls: ['./sidebar.component.scss']
})
export class SidebarComponent  {
    societyadmin = JSON.parse(localStorage['societyadmin']);
    manager;
    managerexists: boolean = false;

    chairman;
    chairmanexists: boolean = false;

     society; 
     
    isActive: boolean = false;
    showMenu: string = '';
    pushRightClass: string = 'push-right';

    constructor( public router: Router) {
        
        if (localStorage.getItem('manager')) {

            this.manager = JSON.parse(localStorage['manager']);
            this.managerexists = true;
 console.log('ManagerExists:' + this.managerexists);

        }

        if (localStorage.getItem('chairman')) {

            this.chairman = JSON.parse(localStorage['chairman']);
            this.chairmanexists = true;
 console.log('ChairmanExists:' + this.chairmanexists);

        }
        this.router.events.subscribe(val => {
            if (
                val instanceof NavigationEnd &&
                window.innerWidth <= 992 &&
                this.isToggled()
            ) {
                this.toggleSidebar();
            }
        });
    }

    refresh(){
        console.log("hi");
    }
    eventCalled() {
        this.isActive = !this.isActive;
    }

    addExpandClass(element: any) {
        if (element === this.showMenu) {
            this.showMenu = '0';
        } else {
            this.showMenu = element;
        }
    }

    isToggled(): boolean {
        const dom: Element = document.querySelector('body');
        return dom.classList.contains(this.pushRightClass);
    }

    toggleSidebar() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle(this.pushRightClass);
    }

    rltAndLtr() {
        const dom: any = document.querySelector('body');
        dom.classList.toggle('rtl');
    }



    onLoggedout() {
        localStorage.removeItem('isLoggedin');
    }
}



